:root {
  --red-dominant-color: #d41443;
  --default-background: #fff;
  --hero-background: #ffb8ca;
  --footer-section-background: #fff5f6;
  --sub-title-color: #ffa5ba;
  --black-color-text: #000;
  --gray-color-text: #907e82;
  --green-card-color: #c2e297;
  --toffee-card-color: #f0d1a5;
}

html, *, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

body {
  color: var(--black-color-text);
  background-color: var(--default-background);
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

@media (min-width: 768px) {
  body {
    font-size: 14px;
  }
}

@media (min-width: 1200px) {
  body {
    font-size: 16px;
  }
}

.visually-hidden, .advantages__title {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section-title {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--red-dominant-color);
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.14;
}

@media (min-width: 768px) {
