  color: var(--red-dominant-color);
  border: none;
  border-radius: 22px;
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.container, .advantages__container {
