.backdrop-header {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(255, 191, 227, .5);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-header .modal {
  max-width: 700px;
  background-color: var(--hero-background);
  padding: 40px 10px;
  position: absolute;
  top: 50%;
  left: 50%;
